import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";

import { addItem, selectItemById } from '../../redux/slices/cartSlice'


const typeNames = ['тонкое', 'традиционное']

export type PizzaBlockProps = {
  id: string;
  name: string;
  imageUrl: string;
  price: number;
  types: number[];
  sizes: number[];
  count: number;
};

const PizzaBlock: React.FC<PizzaBlockProps> = ({
  id,
  name,
  imageUrl,
  price,
  sizes,
  types,
  count,
}) => {
  const dispatch = useDispatch();

  const cartItem = useSelector(selectItemById(id));

  const [activeType, setActiveType] = React.useState(0);
  const [activeSize, setActiveSize] = React.useState(0);

  const onClickAddToCard = () => {
    const item = {
      count,
      id,
      name,
      imageUrl,
      price,
      type: typeNames[activeType],
      size: sizes[activeSize],
    };
    dispatch(addItem(item));
  };
  return (
    <div className="pizza-block">
      <Link to={`pizzas/${id}`}>
        <div className="pizza-block__top">
          <p className="pizza-block__prompt">Перейти к описанию</p>
          <img className="pizza-block__image" src={imageUrl} alt="Pizza" />
          <h4 className="pizza-block__title">{name}</h4>
        </div>
      </Link>
      <div className="pizza-block__selector">
        <ul>
          {types.map((type, i) => (
            <li
              onClick={() => setActiveType(type)}
              className={activeType === type ? "active" : ""}
              key={i}
            >
              {typeNames[type]}
            </li>
          ))}
        </ul>
        <ul>
          {sizes.map((size, i) => (
            <li
              onClick={() => setActiveSize(i)}
              className={activeSize === i ? "active" : ""}
              key={i}
            >
              {size} см.
            </li>
          ))}
        </ul>
      </div>
      <div className="pizza-block__bottom">
        <div className="pizza-block__price">от {price} ₽</div>
        <button
          onClick={onClickAddToCard}
          className="button button--outline button--add"
        >
          <svg
            width="12"
            height="12"
            viewBox="0 0 12 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
              fill="white"
            />
          </svg>
          <span>Добавить</span>
          {cartItem && <i>{cartItem.count}</i>}
        </button>
      </div>
    </div>
  );
};

export default PizzaBlock